<template>
  <div class="ui-block">
    <div class="ui-block-title">
      <h6 class="title">Recommended for you</h6>
    </div>

    <div ref="recommended">
      <!-- Grouped Post -->
      <div v-if="offers.length > 0 " class="row pl-3 pr-3">
        <p class="pl-3 pr-3 col-12">
          Pick from Current Match in Manchester
          <router-link :to="{name: 'related_deals'}"
            v-if="offers_count > 3"
            style="float:right;font-weight:bold;"
            >See all</router-link>
        </p>

        <div
          v-for="(offer, index) in offers"
          :key="index"
          class="col-4"
        >
          <router-link :to="{name: 'deal_page', params: {deal_id: offer.id } }">
            <article class="hentry blog-post">
              <div class="post-thumb mxh-125">
                <img :src=" offer.file === null ? $store.state.staticImage : offer.file " alt="photo" />
              </div>
              <div class="post-content">
                <a href="#" class="post-category bg-mdd">{{ offer.type }}</a>
                <a href="#" class="h4 post-title" style="text-transform: capitalize">{{ $substring(offer.name) }}</a>

                <div class="author-date">
                  Deal Date
                  <a class="h6 post__author-name fn">{{ getTime(offer.deal_date) }}</a>
                </div>
              </div>
            </article>
          </router-link>
        </div>
      </div>
      <!-- .. end Grouped Post -->

      <!-- Grouped Post -->
      <div v-if="hotels.length > 0" class="row pl-3 pr-3">
        <p class="pl-3 pr-3 col-12">
          Best Hotels in {{ venue_address }}
          <a
            v-if="hotels_count > 3"
            style="float:right;font-weight:bold;"
            href="#"
          >See all</a>
        </p>
        <div
          v-for="(hotel, index) in hotels"
          :key="index"
          class="col-4"
        >
          <a href="#">
            <article class="hentry blog-post">
              <div class="post-thumb mxh-125">
                <img :src="$store.state.staticImage" alt="photo" />
              </div>

              <div class="post-content">
                <a href="#" class="post-category bg-mdd">THE COMMUNITY</a>
                <a
                  :href="addressLocation(hotel.formatted_address)"
                  target="_blank"
                  class="h5 post-title"
                  :title="hotel.formatted_address"
                >{{ $substring(hotel.name, '60') }}</a>

                <div class="post-additional-info rec inline-items">
                  <div class="friends-harmonic-wrap" style="flex:1">
                    <ul class="friends-harmonic">
                      <li>
                        <a href="#">
                          <img src="/img/icon-chat27.png" alt="icon" />
                        </a>
                      </li>
                    </ul>
                    <div class="names-people-likes">{{hotel.rating}}</div>
                  </div>
                </div>
              </div>
            </article>
          </a>
        </div>
      </div>
      <!-- .. end Grouped Post -->
    </div>
  </div>
</template>

<script>
var pageData = {
  hotels: {},
  offers: {},
  venue_address: "",
  hotels_count: "",
  offers_count: ""
};

export default {
  data() {
    return pageData;
  },
  created() {
    // this.getRecommended()
  },
  methods: {
    addressLocation(address) {
      // var data = "";
      return address;
    },
    showLoading() {
      let loader = this.$loader(this, this.$refs.recommended, {
        loaderType: "spinner"
      });
      return loader;
    },
    getRecommended() {
      // loader = this.showLoading();
      this.axios
        .get("timeline/offers/recommended")
        .then(response => {
          var res = response.data.data;
          var hotels = res.hotels.results;
          var offers = res.offers;
          // console.log('Offers',offers);
          this.hotels_count = hotels.length;
          this.hotels = hotels.slice(0, 3);
          this.offers_count = offers.length;
          this.offers = offers.slice(0, 3);
          this.venue_address = res.venue_address;
          // console.log(hotels[0].photos.html_attributions);
        })
        .catch(error => {
          // console.log(error.response);
          return error;
        });
    },
    getTime(date) {
      // for this function to be used anywhere just call it as such
      // this.$getDateYear(date), to add extra functions use like so
      // this.$getDateYear(date,{ hour: true, min: true, sec: true })
      // this sets hour to true (to show the hour), same for min and sec
      // set timestamp option if date is in timestamp
      return this.$getDateYear(date,{ hour: true, min: true, sec: true })
    }
  },
  mounted() {
    this.getRecommended();
  }
};
</script>

