<template>
    <!-- Main Content -->
    <div>
        <div ref="newsfeeds" id="newsfeed-items-grid">
            <span style="width: 100%" v-if="feedsStatus === true">
                <timeline-review v-if="timelineReview" :feeds="feeds[0]" :signedIn="signedIn" ></timeline-review>
                <single-review v-if="singleReview" :feeds="feeds" :signedIn="signedIn" ></single-review>
            </span>
            <div v-else class="ui-block">
                <div
                        class="ui-block-content text-center"
                        style="padding: 50px 23px; border-top:solid 4px #08ddc1"
                >
                    <h4 class="title" style="font-weight:bold">Opps! your feeds looks empty</h4>
                    <p>Post reviews about matches you have seen or love</p>
                </div>
            </div>
        </div>
        <a
                v-if="feedsStatus === true && fullFeedsCount > 10"
                id="load-feeds-button"
                href="javascript:;"
                @click="loadMorePost()"
                class="btn btn-control btn-more"
                data-container="newsfeed-items-grid"
        >
            <svg class="olymp-three-dots-icon">
                <use xlink:href="/svg-icons/sprites/icons.svg#olymp-three-dots-icon" />
            </svg>
        </a>
    </div>
    <!-- ... end Main Content -->
</template>

<script>
    import Recommended from "./Recommended";
    import {mapState} from "vuex";
    import jquery from "jquery";
    import timelineReview from "./timelineReview";
    import singleReview from "./singleReview";

    var pageData = {
        moreFeeds: false,
        feeds: [],
        singleReview: '',
        timelineReview: '',
        feedsStatus: false,
        feedImages: "",
        fullFeedsCount: "",
        formModelComment: {
            comment: "",
        },
        user_likes: "",
        signedIn: false,
        withFixture: false,
        next_page_url: '',
        last_page_url: '',
        first_page_url: '',
        current_page: '',
        last_page: '',
        bottom: false,
    };
    var $ = jquery;
    export default {
        name: "Newsfeeds",
        data() {
            return pageData;
        },
        props: ["fixtureId"],
        components: { timelineReview, singleReview },
        watch: {
            // bottom(bottom) {
            //     if (bottom) {
            //         this.loadMorePost()
            //     }
            // }
        },
        created() {
            // window.addEventListener('scroll', () => {
            //     this.bottom = this.bottomVisible()
            // });
            this.getAllReviews();
            window.addEventListener('focus', this.showTweet);
            // Inactive
            // window.addEventListener('blur', stopTimer);
        },
        methods: {
            showTweet() {
                // this.getAllReviews
                const lastFeed = this.feeds[0].created_at;
                this.axios.get(`/timeline/check_feeds/${lastFeed}`)
                .then(response => {
                    // console.log(response.data.data.status)
                    const status = response.data.data.status;
                    window.eventBus.$emit('newReview', status)
                })
                .catch(error => {
                    // console.log(error)
                })
            },
            getTime() {
                // function timeConverter(UNIX_timestamp){
                //   var a = new Date('1571794180' * 1000);
                //   var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
                //   var year = a.getFullYear();
                //   var month = months[a.getMonth()];
                //   var date = a.getDate();
                //   var hour = a.getHours();
                //   var min = a.getMinutes();
                //   var sec = a.getSeconds();
                //   var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec ;
                //   // var tm = jQuery.timeago(new Date(time));
                //   return jQuery(time).timeago();
                // }
                // // console.log(timeConverter(0));
            },
            getUserLikes(likes) {
                let likesHtml = '';
                var likes_count = likes.length;
                if (likes_count > 0 && likes_count === 1) {
                    likesHtml = `
          <a href="#">User</a>
          <br />liked this
        `;
                }else if(likes_count > 1 && likes_count === 3) {
                    likesHtml = `
          <a href="#">You</a>,
          <a href="#">Elaine</a> and
          <br />1 more liked this
        `;
                }else if (likes_count > 3) {
                    likesHtml = `
          <a href="#">You</a>,
          <a href="#">Elaine</a> and
          <br /> ${likes_count - 2} more liked this
        `;
                }else {
                    likesHtml = '';
                }
                // for (let i = 0; i < users.length; i++) {
                //   const element = users[i];
                //   console.log(element);
                // }
                // console.log(likes_count);
                return likesHtml;
            },
            // reset comment box
            checkUserData(userData) {
                if (userData !== "" || userData !== "undefined") {
                    this.userData = JSON.parse(userData);
                    this.signedIn = true;
                }
            },
            bottomVisible() {
                const scrollY = window.scrollY
                const visible = document.documentElement.clientHeight
                const pageHeight = document.documentElement.scrollHeight
                const bottomOfPage = visible + scrollY >= pageHeight
                return bottomOfPage || pageHeight < visible
            },
            getAllReviews() {

                if (this.$props.fixtureId) {
                    this.singleReview = true;
                    this.timelineReview = false;
                    // if fixture id is set, meaning if we are in match page
                    var fid = this.$props.fixtureId;
                    this.axios
                        .get("/timeline/reviews/fixture/" + fid + "/30")
                        .then(response => {
                            var feeds = response.data.data.reviews.data;
                            // console.log('single review',response.data.data.reviews.data)
                            this.feeds = feeds;
                            this.fullFeedsCount = response.data.data.reviews.total;
                            this.feedsStatus = response.data.data.status;
                            this.next_page_url = response.data.data.reviews.next_page_url;
                            this.last_page_url = response.data.data.reviews.last_page_url;
                            this.first_page_url = response.data.data.reviews.first_page_url;
                            // if (this.bottomVisible()) {
                            //     this.getAllReviews()
                            // }
                        })
                        .catch(error => {
                            return error;
                            // console.log(error.response);
                        });
                } else {
                    this.timelineReview = true;
                    this.singleReview = false;
                    this.axios
                        .get("/timeline/reviews/all/30")
                        .then(response => {
                            var feeds = response.data.data.feeds.data;
                            this.feeds.push(feeds);
                            this.fullFeedsCount = response.data.data.feeds_count;
                            // console.log("all reviews", response.data.data);
                            this.feedsStatus = response.data.data.status;
                            this.next_page_url = response.data.data.feeds.next_page_url;
                            this.last_page_url = response.data.data.feeds.last_page_url;
                            this.first_page_url = response.data.data.feeds.first_page_url;
                            this.current_page = response.data.data.feeds.from;
                            this.last_page = response.data.data.feeds.to;
                            // if (this.bottomVisible()) {
                            //     this.getAllReviews()
                            // }
                        })
                        .catch(error => {
                            return error;
                            // console.log(error.response);
                        });
                }
            },
            getMetaDataUrl(url) {
                // console.log(url);
                this.axios
                    .get(`https://api.urlmeta.org/?url=${url}`)
                    .then(response => {
                        // console.log(response);
                    })
                    .catch(error => {
                        // console.log(error);
                    })
            },
            loadMorePost() {
                var page_num = this.current_page;
                var max_pages = this.last_page;
                var next_link = this.next_page_url;
                var container = $("#newsfeed-items-grid");

                // console.log('max page', max_pages)
                // console.log('next link', next_link)

                this.axios
                    .get(`${next_link}`)
                    .then(response => {
                        const feeds = response.data.data.feeds.data;
                        const current_feeds = this.feeds;

                        this.feeds[0].push(feeds)

                        // this.feeds = feeds;
                        // this.fullFeedsCount = response.data.data.feeds_count;
                        // this.feedsStatus = response.data.data.status;
                        // this.next_page_url = response.data.data.feeds.next_page_url;
                        // this.last_page_url = response.data.data.feeds.last_page_url;
                        // this.first_page_url = response.data.data.feeds.first_page_url;
                        // this.current_page = response.data.data.feeds.from;
                        // this.last_page = response.data.data.feeds.to;

                        // var feeds = response.data.data.reviews.data;
                        // this.feeds.push(feeds);
                        // // this.feeds = feeds;
                        // this.next_page_url = response.data.data.reviews.next_page_url;
                        // this.last_page_url = response.data.data.reviews.last_page_url;
                        // this.first_page_url = response.data.data.reviews.first_page_url;
                        // this.current_page = response.data.data.reviews.from;
                        // this.last_page = response.data.data.reviews.to;
                    })
                    .catch(error => {
                        return error;
                        // console.log(error.response);
                    });
            },
        },
        mounted() {
            // check if there is a request to load feeds
            window.eventBus.$on("loadFeeds", val => {
                if(val === true) {
                    this.getAllReviews();
                }
            })
            window.addEventListener('focus', this.showTweet);
            if (this.$props.fixtureId) {
                this.withFixture = true;
            } else {
                this.withFixture = false;
            }

            $("title").html("Welcome to " + this.$store.state.projectName);

            this.getTime();
            let loader = this.$loader(this, this.$refs.newsfeeds, {
                loaderType: "spinner",
            });
            setTimeout(function() {
                loader.hide();
                $("#newsfeed-items-grid").fadeIn();
            }, 500);

            var userData = localStorage.fansData;
            if (userData) {
                this.checkUserData(userData);
            }
        },
    };
</script>

<style>
    .ui-block-title {
        border-bottom: none;
    }
    .fw-1 {
        width: 100%;
    }
    .post-additional-info {
        border-top: none;
        padding: 0;
    }
    .blog-post .post-content {
        padding: 20px 0;
    }
</style>
