                          <div class="post__date">
                            <time
                                    class="published"
                                    :datetime="feed.review_time"
                            >{{ timeAgoFunction(feed.review_time) }}</time>
                          </div>
                        </div>
                    </div>
                    <div class="col">
                        <div class="more" v-if="feed.user_info.username === getUserDetails.username ">
                          <svg class="olymp-three-dots-icon">
                            <use xlink:href="svg-icons/sprites/icons.svg#olymp-three-dots-icon" />
                          </svg>
                          <ul class="more-dropdown">
                            <li>
                              <a @click="updateReview(feed.review_id,index)" data-toggle="modal"
                                 data-target="#edit-my-poll-update" href="javascript:;">Edit Review</a>
                            </li>
                            <li>
                              <a @click="deleteReview(feed.review_id,index)" href="javascript:;">Delete Review</a>
                            </li>
                          </ul>
                        </div>
