                  <!--                  <li v-for="(likes, likeIndex) in feed.likes" :key="likeIndex">-->
                  <!--                    <a href="#">-->
                  <!--                      <img :src=" likes.like.user[0].type === 1 ? likes.like.user[0].avatar : likes.like.user[0].logo" :alt="likes.like.user[0].username" />-->
