                  <!--                  <span v-for="(likes, likeIndex) in feed.likes" :key="likeIndex">-->
                  <!--                      <router-link :to=" '/profile/' + likes.like.user[0].username ">{{ likes.like.user[0].username }}</router-link>,-->
                  <!--                  </span>-->
                  <!--                  <br />likes this-->
                  <!--                </div>-->
                  <!--                  <div v-else-if="feed.likes.length > 1 && feed.likes.length === 3 " class="names-people-likes">-->
                  <!--                  <span v-for="(likes, likeIndex) in feed.likes" :key="likeIndex">-->
                  <!--                      <router-link :to=" '/profile/' + likes.like.user[0].username ">{{ likes.like.user[0].username }}</router-link>,-->
                  <!--                  </span>-->
                  <!--                  <br /> likes this-->
                  <!--                </div>-->
                  <!--                  <div v-else-if="feed.likes.length > 3 && feed.likes.length >= 7 " class="names-people-likes">-->
                  <!--                  <span v-for="(likes, likeIndex) in feed.likes" :key="likeIndex">-->
                  <!--                      <router-link :to=" '/profile/' + likes.like.user[0].username ">{{ likes.like.user[0].username }}</router-link>,-->
                  <!--                  </span>-->
                  <!--                  <br />and {{ feed.likes.length - 3 }} more likes this-->
                  <!--                </div>-->

                <div class="comments-shared">
                  <a href="javascript:;" class="post-add-icon inline-items">
                    <svg class="olymp-speech-balloon-icon">
                      <use xlink:href="/svg-icons/sprites/icons.svg#olymp-speech-balloon-icon" />
                    </svg>
                    <span>{{ feed.comments_count }}</span>
                  </a>
                </div>
              </div>

              <div v-if="signedIn === true" class="control-block-button post-control-button">
                <a
                        href="javascript:;"
                        :id="likeBtn(index)"
                        @click="likeReview(feed.review_id,index)"
                        :class=" feed.liked !== false ? 'btn btn-control liked' : 'btn btn-control' "
                >
                  <svg class="olymp-like-post-icon">
                    <use xlink:href="/svg-icons/sprites/icons.svg#olymp-like-post-icon" />
                  </svg>
                </a>

                <a href="javascript:;" @click="openCommentBox(index)" class="btn btn-control">
                  <svg class="olymp-comments-post-icon">
                    <use xlink:href="/svg-icons/sprites/icons.svg#olymp-comments-post-icon" />
