                    </span>
                    <span v-else>
                    <a
                            class="h6 post__author-name fn"
                            href="#"
                    >{{ userDetail.business_name !== null ? userDetail.business_name : userDetail.username }}</a>

                    </span>
                    <div class="post__date">
                      <time
                              class="published"
                              :datetime="comment.comment.time"
                      >{{ $timeAgo.format(new Date(comment.comment.time * 1000)) }}</time>
                    </div>
                  </div>
                </div>

                <p>{{ comment.comment.comment }}</p>
              </li>
            </ul>

            <form :id="setCommentBoxId(index)" class="comment-form inline-items hide">
              <div class="post__author author vcard inline-items">

                  <span v-if="feed.user_info.type === '1' ">
                    <img :src=" feed.user_info.avatar === null ? reviewUserAvatar(feed.user_info.username) : feed.user_info.avatar" :alt="feed.user_info.username" />
                  </span>
                  <span v-else>
                    <img :src=" feed.user_info.logo === null ? reviewUserAvatar(feed.user_info.username) : feed.user_info.logo " :alt="feed.user_info.username" />
                  </span>

                <div class="form-group with-icon-right">
                  <textarea
                          v-model="formModelComment.comment"
                          class="form-control"
                          placeholder="Write your review here"
                  ></textarea>
                  <span class="commentResponse">fff</span>
                </div>
              </div>

              <button
                      @click="setReviewComment(feed.review_id,index)"
                      type="button"
                      :id="commentBtn(index)"
                      class="btn btn-md-2 btn-primary"
              >Post Comment</button>

              <button
                      type="button"
                      @click="cancleComment(index)"
                      class="btn btn-md-2 btn-border-think c-grey btn-transparent custom-color"
              >Cancel</button>
            </form>

              <!-- .. end Post -->
          </div>
        </span>
    </div>
</template>

<script>
    import {mapState} from "vuex";
    import avta from "@/functions/getAvatar";

    export default {
        name: "singleReview",
        data() {
            return {
                formModelComment: {
                    comment: "",
                },
            }
        },
        props: {
            feeds: Array,
        },
        computed: mapState({
            getUserDetails: state => state.user,
        }),
        mounted() {
            console.log(this.$props)
        },
        methods: {
            // update review
            updateReview(id,index) {
                const data = {
                    review_id: id,
                    index: index,
                    feed: this.feeds[index],
                };
                window.eventBus.$emit("updateReview", data)
            },
            // delete review
            deleteReview(id,index) {
                $("#feed_story_" + index).show();
                this.axios.post('reviews/delete/' + id)
                    .then(response => {
                        // console.log(response)
                        this.feeds.splice(this.feeds.indexOf(index), 1);
                        return response;
                    })
                    .catch(error => {
                        // console.log(error)
                        return error;
                    })
            },

            // get fixtures for i=fixture id
            getFixtures(name1,name2) {
                return name1 + " (VS) " + name2;
            },
            cancleComment(index) {
                $("#comment" + index + " textarea").val("");
                $("#comment" + index + " .commentResponse").html("");
            },
            // post users review comment
            setReviewComment(review_id, index) {
                var comment = this.formModelComment.comment;
                const request_payload = {
                    comment: comment,
                    review_id: review_id
                };
                $("#commentBtn_" + index).html("Submitting...");
                $("#commentBtn_" + index).attr("disabled", true);
                $("#comment" + index + " .commentResponse").html("");
                this.axios
                    .post("review/comment/add", request_payload)
                    .then(response => {
                        // console.log('response ', response.data.message);
                        $("#commentBtn_" + index).html("Post Comment");
                        $("#commentBtn_" + index).attr("disabled", false);
                        $("#comment" + index + " textarea").val("");
                        $("#comment" + index + " .commentResponse").html(
                            response.data.message
                        );

                        // toggle bet show and hide of comment list
                        $(".comments-list").toggleClass("hide");
                        // toggle bt hide and show of comment box
                        $("#comment" + index).toggleClass("hide");
                        // clear response message
                        $("#comment" + index + " .commentResponse").html("");
                        // clear text box
                        $("#comment" + index + " textarea").val("");

                    })
                    .catch(error => {
                        // console.log(error);
                        $("#commentBtn_" + index).html("Post Comment");
                        $("#commentBtn_" + index).attr("disabled", false);
                        $("#comment" + index + " .commentResponse").html(
                            error.response.data.data
                        );
                    });
            },
            // set id for individual review post card
            commentBtn(index) {
                return "commentBtn_" + index;
            },
            // set id for individual review post card
            likeBtn(index) {
                return "likeBtn_" + index;
            },
            // like a review post
            likeReview(feed_id, index) {
                var api;
                if ($("#likeBtn_" + index).hasClass("liked")) {
                    api = `review/unlike/${feed_id}`;
                    // unlike the post
                    this.axios
                        .get(api)
                        .then(response => {
                            // console.log(response);
                            $("#likeBtn_" + index).removeClass("liked");
                            var likeVal = $("#like_count_" + index).html();
                            var calcu = isNaN(parseInt(likewhVal)) ? 0 : parseInt(likeVal);
                            var sum = calcu - 1;
                            $("#like_count_" + index).html(sum);
                            return response;
                        })
                        .catch(error => {
                            return error;
                            // console.log(error);
                        });
                } else {
                    api = `review/like/${feed_id}`;
                    // like the post
                    this.axios
                        .get(api)
                        .then(response => {
                            // console.log(response);
                            $("#likeBtn_" + index).addClass("liked");
                            var likeVal = $("#like_count_" + index).html();
                            var calcu = isNaN(parseInt(likeVal)) ? 0 : parseInt(likeVal);
                            var sum = calcu + 1;
                            $("#like_count_" + index).html(sum);
                            return response;
                        })
                        .catch(error => {
                            return error;
                            // console.log(error);
                        });
                }
            },
            // time ago function
            timeAgoFunction(timestamp) {
                return this.$timeAgo.format(new Date(timestamp * 1000));
            },
            // open individual comment box
            openCommentBox(index) {
                // toggle bet show and hide of comment list
                $(".comments-list").toggleClass("hide");
                // toggle bt hide and show of comment box
                $("#comment" + index).toggleClass("hide");
                // clear response message
                $("#comment" + index + " .commentResponse").html("");
                // clear text box
                $("#comment" + index + " textarea").val("");
            },
            setCommentBoxId(index) {
                return "comment" + index;
            },
            reviewUserAvatar(avatar) {
                return avta({local:{ av: null, us: avatar }});
            },
            urlify(text) {
                var urlRegex = /(((https?:\/\/)|(www\.))[^\s]+)/g;
                if (text === null || text === '') {
                    text = '';
                }

                let string = text.replace(urlRegex, function(url,b,c) {
                    var url2 = (c == 'www.') ?  'http://' +url : url;
                    return '<a href="' +url2+ '" target="_blank">' + url + '</a>';
                });
                return string;
            },
            getTime() {
                // function timeConverter(UNIX_timestamp){
                //   var a = new Date('1571794180' * 1000);
                //   var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
                //   var year = a.getFullYear();
                //   var month = months[a.getMonth()];
                //   var date = a.getDate();
                //   var hour = a.getHours();
                //   var min = a.getMinutes();
                //   var sec = a.getSeconds();
                //   var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec ;
                //   // var tm = jQuery.timeago(new Date(time));
                //   return jQuery(time).timeago();
                // }
                // // console.log(timeConverter(0));
            },
        },
    }
</script>

